<template>
  <div class="wrap">
    <Nav />

    <van-pull-refresh v-model="isLoading"
                      success-text="刷新成功"
                      @refresh="onRefresh">
      <van-panel v-for="(joke, index) in content"
                 :key="index"
                 :title="joke.content">
      </van-panel>
    </van-pull-refresh>
  </div>
</template>
<script>
import Nav from '@/components/Nav.vue'

export default {
  components: {
    Nav
  },
  data() {
    return {
      content: [],
      // 下拉刷新
      isLoading: false
    }
  },
  created() {
    this.getJokes()
  },
  methods: {
    getJokes() {
      this.$axios({
        methds: 'GET',
        url: 'https://v.juhe.cn/joke/randJoke.php',
        params: {
          key: 'e2ad5e614e01ac8b69c8059e4b618f4b'
        }
      }).then(res => {
        let { result } = res.data
        this.content = result
      })
    },
    onRefresh() {
      this.getJokes()
      this.isLoading = false
    }
  }
}
</script>
<style lang="scss" scoped>
.van-panel {
  margin-bottom: 10px;
  padding: 6px;
  border-radius: 3px;
  box-shadow: 0 0 3px 4px rgba(233, 233, 233, 0.3);
}
</style>
