<template>
  <div>
    <Nav />
    <form action="/">
      <van-search v-model="city"
                  placeholder="请输入想要查询的城市(如成都，上海，深圳)"
                  shape="round"
                  @search="onSearch"
                  @cancel="onCancel" />
    </form>
    <van-panel v-if="toShow"
               :title="new_city"
               desc="当前天气详情情况">
      <div class="des">
        <div>
          <van-tag size="large"
                   color="#07c160">天气：{{realtime.info}}</van-tag>
        </div>
        <div>
          <van-tag size="large"
                   color="#f2826a">温度：{{realtime.temperature}}°C</van-tag>
        </div>
        <div>
          <van-tag size="large"
                   color="#7232dd">湿度：{{realtime.humidity}}%</van-tag>
        </div>
      </div>
      <div slot="footer">
        <van-button @click="toOpen"
                    size="small"
                    color="linear-gradient(to right, #4bb0ff, #6149f6)">查看近5天天气情况</van-button>
      </div>
    </van-panel>
    <!-- 近5天天气情况 -->
    <transition name="van-fade">
      <div v-if="detail"
           class="five_days">
        <van-panel v-for="(item, index) in future"
                   :key="index"
                   :title="new_city"
                   desc="近5天天气情况"
                   :status="item.date">
          <div class="des">
            <div>
              <van-tag size="large"
                       color="#07c160">天气：{{item.weather}}</van-tag>
            </div>
            <div>
              <van-tag size="large"
                       color="#f2826a">温度：{{item.temperature}}</van-tag>
            </div>
            <div>
              <van-tag size="large"
                       color="#7232dd">凤向：{{item.direct}}</van-tag>
            </div>
          </div>
        </van-panel>
      </div>
    </transition>

  </div>
</template>
<script>
import Nav from '@/components/Nav.vue'

export default {
  components: {
    Nav
  },
  data() {
    return {
      // 5天详情是否展示
      detail: false,
      toShow: false,
      city: '',
      new_city: '',
      realtime: {},
      future: []
    }
  },
  created() {},
  methods: {
    getWeather() {
      this.$axios({
        method: 'GET',
        url: 'https://apis.juhe.cn/simpleWeather/query',
        params: {
          city: this.city,
          key: 'd0e46a1d29217c2dae2a98c9829f7f8a'
        }
      })
        .then(res => {
          this.realtime = res.data.result.realtime
          let { city, future } = res.data.result
          this.new_city = city
          this.future = future
          this.$toast.success('查询成功')
          this.toShow = true
        })
        .catch(err => {
          this.$toast.fail('查询失败')
          console.log(err)
        })
    },
    onSearch(city) {
      this.city = city
      this.getWeather()
    },
    onCancel() {},
    toOpen() {
      this.detail = !this.detail
    }
  }
}
</script>
<style lang="scss" scoped>
.des {
  padding: 6px;

  > div {
    margin-bottom: 10px;
    font-size: 16px;
  }
}
</style>
