<template>
  <div>
    <van-notice-bar background="#ecf9ff"
                    wrapable
                    @click="toToutiao"
                    mode="closeable">
      {{ ads }}
    </van-notice-bar>
    <van-divider :style="{
				color: '#1989fa',
				borderColor: '#1989fa',
				padding: '0 16px'
			}">
      小玩意
    </van-divider>
  </div>
</template>
<script>
export default {
  data() {
    return {
      scroll: true,
      news: null
    }
  },
  created() {
    this.getNews()
  },
  computed: {
    ads() {
      return this.news ? this.news.title + '  ---实时新闻来自头条' : '占位...'
    }
  },
  methods: {
    getNews() {
      this.$axios({
        method: 'get',
        url: 'https://v.juhe.cn/toutiao/index',
        params: {
          type: 'keji',
          key: '04d3bfc06491bd4a3d3a97300fc56162'
        }
      })
        .then(res => {
          let { data } = res.data.result
          this.news = data[Math.floor(Math.random() * Math.floor(30))]
        })
        .catch(err => {
          this.$notify({
            message: '接口出错，请联系管理员！',
            color: '#ad0000',
            background: '#ffe1e1',
            duration: 5000
          })
          console.log(err)
        })
    },
    toToutiao() {
      this.$dialog
        .confirm({
          title: '注意',
          message: '此操作将会离开本页面，跳转到外链新闻页'
        })
        .then(() => {
          location.href = this.news.url
        })
        .catch(() => {
          // on cancel
        })
    }
  }
}
</script>
<style lang="scss" scoped></style>
