import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Jokes from '../views/Jokes.vue'
import Weather from '../views/Weather.vue'
Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'home',
		component: Home
	},
	{
		path: '/jokes',
		name: 'jokes',
		component: Jokes
	},
	{
		path: '/weather',
		name: 'weather',
		component: Weather
	}
]

const router = new VueRouter({
	routes
})

export default router
